header{
    width: 100vw;
    height: 100px;
}

nav{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 10px;
    align-items: center;
}

.navigator, .buttons {
    display: none;
}

.logo-img{
    width: 120px;
    align-items: center;
    display: flex;
}

.logo-img img{
    width: 100%;
}

.responsive-menu .btn{
    font-size: 40px;
    background-color: transparent;
    color: #232323;
    border: none;
    padding: 0;
}

ul li{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

ul li a{
    color: #232323;
    font-size: 20px;
}

.buttons-menu{
    width: 350px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
}
.link{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.buttons-menu .btn, .buttons .btn{
    background-color: #232323;
    color: white !important;
    border: none;
}

.buttons-menu .link a, .buttons-menu .btn, .buttons .link a, .buutons .btn{
    color: #232323;
    font-size: 18px;
    width: 160px;
    --bs-btn-active-bg: #4b4b4b;
}

@media screen and (min-width: 1024px) {
    .responsive-menu{
        display: none;
    }

    .navigator, .buttons {
        display: flex;
    }

    .navigator ul li, .navigator-menu{
        flex-direction: row;
    }

    nav{
        padding: 10px 30px;
    }
}



